import React from 'react';
import { getItem, useResultDispatch, useResultState, useUsersDispatch } from '../ResultContext';
import styled from 'styled-components';

const ItemWrap = styled.div`
    padding-left:55px;
    width:80%;
`;
const NoImage = styled.div`
    padding:100px;
    text-align:center;
`;

function ResultItem() {
    const state = useResultState();
    const dispatch = useResultDispatch();

    const { loading, data: item, error } = state.item;

    if (loading) return <div>로딩중...</div>;
    if (error) return <div>에러가 발생했습니다</div>;
    if (!item) return <div>결과가 없습니다.</div>;

    console.log(item);

    const Unix_timestamp = (unixTime) => {
        let date = new Date(unixTime * 1000);
        let year = date.getFullYear();
        let month = "0" + (date.getMonth() + 1);
        let day = "0" + date.getDate();
        let hour = "0" + date.getHours();
        let minute = "0" + date.getMinutes();
        let second = "0" + date.getSeconds();
        return `${year}-${month.substr(-2)}-${day.substr(-2)} ${hour.substr(-2)}:${minute.substr(-2)}:${second.substr(-2)}`;
    }

    return (
        <ItemWrap>
            <h2>{item.title}</h2>
            {item.media.image_url &&
                <img src={item.media.image_url} style={{ width: '500px' }} />
            }
            {!item.media.image_url &&
                <NoImage>There's no image</NoImage>
            }
            <p><b>Occurred at: </b>{Unix_timestamp(item.occurred_at)}</p>
            <p><b>Updated at: </b>{Unix_timestamp(item.updated_at)}</p>
            <p><b>Address: </b>{item.address}</p>
            <p><b>Type: </b>{item.type}</p>
            <p>{item.description}</p>
        </ItemWrap>
    );
}

export default ResultItem;